body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.infoGrid .bigAvatar {
    margin: 10px;
    width: 200px;
    height: 200px;
    background-image: url(/personal-website/static/media/self.b4e29e01.jpeg);
    border: 3px solid rgba(232, 232, 232, 0.8);
    box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, 0.75) inset;
    -moz-box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, 0.75) inset;
    -webkit-box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, 0.75) inset;
}

.infoGrid .name {
    text-align: center;
    color: white
}

.infoGrid .position {
    text-align: center;
    margin-top: 5px;
    color: white;
    font-style: italic;
}

.infoGrid .introduction {
    color: #4e4c4c;
    margin: 15px 70px;
    text-align: center;
}

.infoGrid .divider {
    margin: 0;
    border: none;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    background-color: white;
    width: 320px;
}

.infoGrid .infoList {
    margin: 10px 114px;
    color: white;
}

.infoGrid .socialMedias {
    text-align: center;
    margin-top: 25px;
}

.infoGrid .socialMediasIcon {
    margin: 0 20px;
}
.drawer {
  width: 500px;
}

.drawer .drawerPaper {
  width: 500px;
  background-color: #2196f3;
  box-shadow: 0.5px 0px 11px 0px rgba(0,0,0, 1), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 3px 3px -2px rgba(0,0,0,0.12);
}


.experienceContainer {
    padding: 30px 30px;
}

a:hover {
    text-decoration: none;
}

.profile-pic {
    height: 4vmin;
    margin-right: 10px;
    border-radius: 50px;
}

div.media {
    margin-top: 30px;
    margin-bottom: 50px;
}

div.media .media-heading .jobTotalDuration {
    color: #666;
    font-size: 14px;
    display: block;
}

div.media img {
    height: 18vmin;
    width: 18vmin;
    margin-right: 40px;
    border-radius: 50px;
}

div.media .jobDuration {
    color: #444;
    display: block;
}

div.media .jobLocation {
    color: #666;
}

.formLabel {
    font-weight: bold;
}

.certificateLink {
    color: cornflowerblue;
}

footer {
    margin-top: 20px;
    margin-bottom: 10px;
    border-top: #444444;
    text-align: center;
}

footer a {
    color: black;
    font-size: 30px;
    margin-right: 10px;
    margin-left: 10px;
}

.skillChip {
    margin: 8px;
}

.skillChip span {
    font-weight: bold;
    font-size: 14px;
}

.tabPanelRoot .skillPanelDetail {
    display: inline-block;
}

.tabPanelRoot .projectPanel {
    padding: 40px;
}

.projectDescription, .projectName {
    pointer-events: none;
}

.openDetail {
    outline: none !important;
}

